@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Lato",sans-serif;
}

.home,.reports,.products{
  display: flex;
  height: 90vh;
  align-items: center;
   justify-content: center;
   font-size: 3rem;
}
.navbar{
    background-color: black;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
}

.menu-bars{
    margin-left: 2rem;
    font-size: 2rem;
    background: none;
}
.nav-menu{
    background-color:black;
    width:250px;
    height:100vh;
    display:flex;
    justify-content: center;
    position:fixed;
    top:0;
    left:-100%;
    transition: 850ms;
}
.nav-menu.active{
    left:0;
    transition: 350ms;

}
.nav-text{
    display:flex;
    justify-content: start;
    align-items: center;
    padding:8px 0px 8px 16px;
    list-style: none;
    height: 60px;
}
.nav-text a{
    text-decoration: none;
    color: white;
    font-size: 18px;
    width:95%;
    height: 100%;
    display:flex;
    align-items:center;
    padding:0px 16px ;
    border-radius: 4px;

    
}
.nav-text a:hover{
    background-color: teal;

}
.nev-menu-items{
    width:100%;
}
.nav-toggle{
    background-color:black ;
    width:100%;
    height:80px;
    display: flex;
    justify-content: start;
    align-items: center;

}
span{
    margin-left: 16px;
}
.img-div{
    background-color:black;
    width:90%;
}
.img-comp{
    background-color: rgb(87, 87, 78);
    width:95%;
    padding: 20px;
    margin-top: 2.5%;
    margin-bottom: 2.5%;
    margin-left:2.5%;
    margin-right:2.5% ;
    
}


